import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { ParallaxProvider } from 'react-scroll-parallax';
import { Helmet } from 'react-helmet';
import avt from "./avt.png";

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <ParallaxProvider>
      <Helmet>
        <meta name="title" content="Ca Vĩnh Thuận" />
        <meta name="description" content="Hello! this is my profile page, learn more about me! ♥" />
        <meta name="keywords" content="cavinhthuan, personal profile, portfolios, Ca Vĩnh Thuận, hello" />
        <meta name="robots" content="index, nofollow" />
        <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
        <meta name="language" content="English" />
        <meta name="revisit-after" content="5 days" />
        <meta name="author" content="Ca Vĩnh Thuận" />
        <link rel="icon" href={avt} />
        <title>Ca Vĩnh Thuận</title>
        <link rel="canonical" href="https://profile.cavinhthuan.xyz" />
        <meta property="og:image" content={avt} />
        <meta property="og:image:height" content="200px" />
      </Helmet>
      <App />
    </ParallaxProvider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
